<template>
  <div class="h-6" />
  <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      @select="handleSelect"
  >
    <div class="logoIn"></div>
    <div style="width: 385px;float: right;" ></div>
    <div class="time"> :{{nowTime}}</div>


  </el-menu>
  <!--  -->
    <div style="display: flex;">
      <!--  -->
      <div style="width: 150px; min-height: calc(100vh - 62px); overflow: hidden;background-color: white; margin-right: 2px;float: left;">
        <el-menu :default-active="$route.path" router class="el-menu-demo" style="margin-bottom: 10px;" >
      <el-menu-item index="1" @click="return1()">
      </el-menu-item>

      <el-sub-menu index="2" >
      <template #title open></template>
        <el-menu-item index="2-1" @click="adminpersonalInfo()"></el-menu-item>
        <el-menu-item index="2-2"@click="adminuserManagement()"></el-menu-item>
        <el-menu-item index="2-3" @click="adminbookManagement()" ></el-menu-item>
        <el-menu-item index="2-3" @click="changePassword_fun()"></el-menu-item>
        <el-menu-item index="2-4" @click="logout()"></el-menu-item>
      </el-sub-menu>
           </el-menu>

      </div>
      <div style="width: 100%;">
      <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="recordId" label="" width="60px"></el-table-column>
      <el-table-column prop="id" label="Id" width="70px"></el-table-column>
      <el-table-column prop="name" label="" width="70px"></el-table-column>
      <el-table-column prop="isbn" label="isbn" width="57px" ></el-table-column>
      <el-table-column
          prop="bookNumber"
          label="()"
          width="148px">
      </el-table-column>
      <el-table-column prop="bookname" label=""></el-table-column>
      <el-table-column prop="requestId" label="" width="60px"></el-table-column>
      <el-table-column prop="borrowTime" label=""></el-table-column>
      <el-table-column prop="returnTime" label=""></el-table-column>
      <el-table-column prop="returnState_C" label=""width="80px"></el-table-column>
      <el-table-column prop="isTimeOut" label="" width="80px"></el-table-column>
      <el-table-column prop="penalty" label="" width="80px"></el-table-column>
      <el-table-column label="">
        <template v-slot="scope">
          <el-button
              type="primary"
              value="return"
              @click="show1(scope.row)"
          ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
      </div>
    </div>
  <div>

  </div>

  <el-dialog title="" v-model="dialogVisible" width="30%">
    <el-form :model="form" label-width="120px">
      <el-form-item label="">
        <el-input v-model="form.penalty" style="width: 80%"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
<span class="dialog-footer">
<el-button @click="dialogVisible = false"> </el-button>
<el-button type="primary" @click="save"> </el-button>
</span>
    </template>
  </el-dialog>
</template>

<script>
// axios @src
import request from "@/utils/request";
import AboutView from "@/views/AboutView.vue";


// @ is an alias to /src
export default {
  name: 'adminUserManagement',
  components: {
  },
  data() {
    return{
      userId: this.$route.query.userId,//id
      dialogVisible:false,
      tableData:[],
      form:{},
      recordId:"",
      penalty:"",
      nowTime: '',
      timerId: null, // ID
    }
  },
  //mountednowTimesbeforeDestroyclear
  mounted () {
    this.nowTimes()
    this.timerId = setInterval(this.echoFunction, 6000); // 10echoFunction 1000=1s
  },
  beforeDestroy () {
    this.clear()
  },
  unmounted() {
    clearInterval(this.timerId); // 
  },
  created() {
    this.list()
    },
  methods: {
    echoFunction() {
      //  10s 10
      request.get("/api/getRecordsInfo").then(res => {
        res.data.forEach(item => {
          // alert(item.returnTime)
          if (item.returnTime!=null){ //
            // item.borrowTimeitem.returnTimeISO 8601
            let borrowTime = new Date(item.borrowTime); // Date
            let returnTime = new Date(item.returnTime); // Date
            // 
            if (isNaN(borrowTime.getTime()) || isNaN(returnTime.getTime())) {
              console.error('Invalid date string');
              return;
            }
            // 
            let timeDiffInMilliseconds = borrowTime - returnTime;
            // 
            let timeDiffInSeconds = Math.abs(timeDiffInMilliseconds / 2000); // Math.absreturnTimeborrowTime
            // console.logalertalertUI
            // alert(`${timeDiffInSeconds}`);
            // DOMtextContentinnerHTML
            // document.getElementById('timeDifference').textContent = timeDiffInSeconds;
            if(timeDiffInSeconds>6&&item.returnState===0){ //
              // alert("") 30s
              //itemrecords1
              // alert("item.recordId"+item.recordId);
              request.put("/api/isTimeOut" ,null,{params:{recordId:item.recordId}}).then(res => {
                this.$message({
                  type:"info", message: ""
                })
                request.put("/api/PenaltyAdd" ,null,{params:{recordId:item.recordId}}).then(res=>{
                  this.$message({
                    type:"success", message: ""
                  })
                })
              })
              this.list()
            }

          }else{
            //
            // alert("")
            // Date
            let borrowTime = new Date(item.borrowTime);
            // Date
            let currentTime = new Date();
            // 
            if (isNaN(borrowTime.getTime()) || isNaN(currentTime.getTime())) {
              console.error('Invalid date string');
              return null;
            }
            // 
            let timeDiffInMilliseconds = currentTime - borrowTime;
            // 
            let timeDiffInSeconds_0 = Math.abs(timeDiffInMilliseconds / 1000);
            // alert("currentTime"+ currentTime);
            // alert(`${timeDiffInSeconds}`);
            if(timeDiffInSeconds_0>6&&item.returnState===0){ //
              // alert("") 30s
              //itemrecords1
              // alert("item.recordId"+item.recordId);
              request.put("/api/isTimeOut" ,null,{params:{recordId:item.recordId}}).then(res => {
                this.$message({
                  type:"info", message: ""
                })
                request.put("/api/PenaltyAdd" ,null,{params:{recordId:item.recordId}}).then(res=>{
                  this.$message({
                    type:"success", message: ""
                  })
                })
              })
              this.list()
            }

          }

        });

      }).catch(error => {
        // 
        console.error('Request failed:', error);
      });
    },
    //
    timeNow(timeStamp) {
      let year = new Date(timeStamp).getFullYear();
      let month =new Date(timeStamp).getMonth() + 1 < 10? "0" + (new Date(timeStamp).getMonth() + 1): new Date(timeStamp).getMonth() + 1;
      let date =new Date(timeStamp).getDate() < 10? "0" + new Date(timeStamp).getDate(): new Date(timeStamp).getDate();
      let hh =new Date(timeStamp).getHours() < 10? "0" + new Date(timeStamp).getHours(): new Date(timeStamp).getHours();
      let mm =new Date(timeStamp).getMinutes() < 10? "0" + new Date(timeStamp).getMinutes(): new Date(timeStamp).getMinutes();
      let ss =new Date(timeStamp).getSeconds() < 10? "0" + new Date(timeStamp).getSeconds(): new Date(timeStamp).getSeconds();
      this.nowTime = year + "" + month + "" + date +"  "+""+''.charAt(new Date().getDay())+" "+hh+":"+mm+':'+ss ;
    },
    // 
    nowTimes(){
      this.timeNow(new Date());
      setInterval(this.nowTimes,1000);
      this.clear()
    },
    //
    clear(){
      clearInterval(this.nowTimes)
      this.nowTimes = null;
    },
    // Date
// JavaScriptDateISO 8601
parseISO8601(str) {
  var parts = str.split('T'),
      dateParts = parts[0].split('-'),
      timeParts = parts[1].split(':'),
      timePartsLen = timeParts.length;
  if (timePartsLen === 2) {
    timeParts.push('00'); // 
  }
  return new Date(Date.UTC(
      parseInt(dateParts[0], 10),
      parseInt(dateParts[1], 10) - 1, // 0
      parseInt(dateParts[2], 10),
      parseInt(timeParts[0], 10),
      parseInt(timeParts[1], 10),
      parseInt(timeParts[2], 10)
  ));
},
save(){
          // alert(this.form.penalty)
          // alert(this.recordId)
      request.put("/api/changePenalty", null,{params:{penalty:this.form.penalty,recordId:this.recordId}}).then(res=>{
        this.$message({
          type: "success", message: ""
        })
      })
      this.dialogVisible=false //
      this.list() // 
      this.list() // 
      this.list() // 

    },
    show1(row){
        this.dialogVisible = true;
        // alert(row.recordId)
      this.recordId=row.recordId;
    },
    list(){
      request.get("/api/getRecordsInfo").then(res => {
        console.log("================================1")
        console.log(res.data)

        // 
        let updatedData = res.data.map(item => {
          //  Promise 
          return Promise.all([
            request.get("/api/getName", { params: { id: item.id } }),
            request.get("/api/getBookName", { params: { isbn: item.isbn } })
          ]).then(([res1, res2]) => {
            // 
            item.name = res1.msg;
            item.bookname = res2.msg;
            if(item.returnState===0){
              item.returnState_C=""
            }else if(item.returnState===1){
              item.returnState_C=""
            }
            if(item.returnTime===null){
              item.returnTime="null"
            }
            if(item.isTimeOut===0){
              item.isTimeOut=""
            }else if(item.isTimeOut===1){
              item.isTimeOut=""
            }
            return item; // 
          });
        });

        //  Promise.all 
        Promise.all(updatedData).then(updatedItems => {
          //  tableData
          this.tableData = updatedItems;
          console.log("================================2")
          console.log(this.tableData) // 
        }).catch(error => {
          console.error("Error fetching data:", error);
        });

      }).catch(error => {
        console.error("Error fetching records info:", error);
      });

    },
    return1() {
      console.log("")
      this.$router.push({path:'/adminhome',query: { userId:this.userId}});
    },
    changePassword_fun() {
      console.log("")
      this.$router.push({path:'/adminchangePassword',query: { userId:this.userId}});
    },
    logout() {
      console.log("")
      this.$router.push({path:'/',query: { userId:this.userId}});
    },
    adminpersonalInfo(){
      this.$router.push({path:'/adminpersonalInfo',query: { userId:this.userId}})
    },
    adminuserManagement(){
      this.$router.push({path:'/adminUserManagement',query: { userId:this.userId}})
    },adminbookManagement() {
      console.log("")
      this.$router.push({path:'/adminBookManagement',query: { userId:this.userId}});
    }
  }
}
</script>
<style>
.time{
  width: 450px;
  font-size: 16px;
  color: white;
  float: right;
  margin: 30px 70px;
}
</style>
