<template>
  <div class="img_box"/> <!--  -->
  <div class="flex-center"> <!--  flex-center  -->
    <el-form class="login-container">
      <h1 class="title"></h1>
      <el-form-item >
        <el-input type="text" v-model="name"  placeholder="" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item >
        <el-input type="password" v-model="pwd" placeholder="" show-password autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item >
        <el-input type="text" v-model="mobile" placeholder="" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item >
        <el-input type="text" v-model="email" placeholder=""  autocomplete="off"></el-input>
      </el-form-item>




      <el-form-item label="" style="margin-left: 20px;color: #eee;">
        <el-radio-group v-model="gender">
          <el-radio style="margin-right: 20px"  label="1"></el-radio>
          <el-radio  label="0"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item style="margin-top: 30px;">
        <el-button type="primary" style="width:100%;"
                   @click="Register()">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
      </el-form-item>
      <el-row style="text-align: center;margin-top:-10px">
        <el-link style="margin-right: 150px"  type="primary" @click="forgetPass()"></el-link>
        <el-link type="primary" @click="Login()"></el-link>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import { h } from 'vue'
import { ElMessage } from 'element-plus'
import request from "@/utils/request";
export default {
  name: 'Register',
  data() {
    return {
      name: "",
      pwd: "",
      mobile: '',
      gender: 0, //1 0 
      email: '',

      msg: '',
      rules: {
        realname: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
          {
            min: 3,
            max: 18,
            message: ' 3  18 ',
            trigger: 'blur'
          }
        ]
      }
    }

  },
  methods: {
    forgetPass(){
      this.$message({ // 
        type: "info", message: ""
      })
    },
    open() {
      console.log('open')
      ElMessage('This is a message666.')
    },
    Login() {
      this.$router.push('/');
    },
    Register() {
      let  Register= {
        name: this.name,
        pwd:this.pwd,
        mobile: this.mobile,
        gender: this.gender,
        email: this.email,
      };
      if(this.name===""||this.pwd===""||this.email===""||this.mobile===""||this.gender===""){
        this.$message({
          type: "error", message: ""
        })}else
        {
          request.post("/api/register",Register).then(res=>{
            console.log(Register);
            if(res.code==="200"){
              this.$message({//
                type: "success", message: res.msg})
              this.$router.push({ path: '/userhome', query: { name: this.name } });//
            }
          else{
            this.$message({//
              type: "error", message: ""})
          }
        }).catch(error=>{
        console.log(Register);
        console.log(this.name+this.pwd);
        console.log(error);
      })
        }


    }
  }
}
</script>

<style scoped>

.img_box{ /**/
  width: 100%;height: 100%;
  background-image: url(https://library-weibb.oss-cn-hangzhou.aliyuncs.com/Front/%E6%B3%A8%E5%86%8C%E7%8C%AB%E7%8C%AB.jpg);
  background-size: cover; /*  */
  position: absolute; /*  */
  /* overflow: hidden; */
  /* overflow: auto; */
  z-index: -1;
  background-repeat: no-repeat;
}
/*  */

/**/
.flex-center {
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  height: 100vh; /*  */
}

.login-container {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background-color: rgba(255, 255, 255, 0.7); /*  */
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: dodgerblue;;
}
</style>
