<template>
  <div class="h-6" />
  <div class=""></div>
  <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo "
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"

      @select="handleSelect"
  >
  <div class="logoIn"></div>
  </el-menu>

   <!--  -->
    <div style="display: flex;">
      <!--  -->
      <div style="width: 150px; min-height: calc(100vh - 62px); overflow: hidden;background-color: white; margin-right: 2px;float: left;">
        <el-menu :default-active="$route.path" router class="el-menu-demo" style="margin-bottom: 10px;" >
      <el-menu-item index="1" @click="return1()">
      </el-menu-item>

      <el-sub-menu index="2" >
      <template #title open></template>
        <el-menu-item index="2-1" @click="adminpersonalInfo()"></el-menu-item>
        <el-menu-item index="2-2"@click="adminuserManagement()"></el-menu-item>
        <el-menu-item index="2-3" @click="adminbookManagement()" ></el-menu-item>
        <el-menu-item index="2-3" @click="changePassword_fun()"></el-menu-item>
        <el-menu-item index="2-4" @click="logout()"></el-menu-item>
      </el-sub-menu>
           </el-menu>

      </div>

      <div style="width: 100%;">
        <div>
          <!-- allitemall -->
          <p>Sum: {{ all }}</p>
        </div>
        <el-button class="upload-btn" @click="addBook()"></el-button>
        <el-table :data="tableData" style="width: 160%" >
      <el-table-column prop="isbn" label="ISBN"  width="60px"></el-table-column>
      <el-table-column prop="bookname" label="" width="200px"></el-table-column>
      <el-table-column prop="category" label=""></el-table-column>
      <el-table-column prop="author" label=""></el-table-column>
      <el-table-column prop="publish" label=""></el-table-column>
      <el-table-column prop="img" label="">
        <template #default="scope">
          <img :src="scope.row.img" alt="" style="width: 100px; height: auto;">
        </template>
      </el-table-column>
      <el-table-column prop="numss" label=""></el-table-column>
      <el-table-column label="">
        <template v-slot="scope">
          <el-button type="primay" value="return" @click="show1(scope.row)"></el-button>

          <el-button type="primary" value="return" @click="downBook(scope.row)" class="c1"></el-button></template>
      </el-table-column>

    </el-table>
      </div>
    </div>

  <el-dialog title="" v-model="dialogVisible1" width="30%">
    <el-form :model="form1" label-width="120px">
      <el-form-item label="">
        <el-input v-model="form1.bookname" style="width: 80%"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form1.category" style="width: 80%"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form1.author" style="width: 80%"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form1.publish" style="width: 80%"></el-input>
      </el-form-item>
      <el-form-item label="">
          <el-upload
              class="avatar-uploader"
              action="/api/uploadAvatarBook"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
              :data="{isbn:this.isbn}"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
      </el-form-item>
      <el-form-item label="" >
        <el-input v-model="form1.addnums" style="width: 80%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" >
        <el-input v-model="form1.subnums" style="width: 80%" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
<span class="dialog-footer">
<el-button @click="dialogVisible1 = false"></el-button>
<el-button type="primary" @click="changeBookIfo"> </el-button>
</span>
    </template>
  </el-dialog>

  <el-dialog title="" v-model="dialogVisible" width="30%" >
    <el-form :model="form" label-width="120px" >
      <el-form-item label="">
        <el-input v-model="form.bookname" style="width: 80%"placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.category" style="width: 80%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.author" style="width: 80%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.publish" style="width: 80%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.allnums" style="width: 80%"  placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
<span class="dialog-footer">
<el-button @click="dialogVisible = false"></el-button>
<el-button type="primary" @click="save"></el-button>
</span>
    </template>
  </el-dialog>

</template>

<script>
import Cookies from 'js-cookie'
// axios @src
import request from "@/utils/request";
import AboutView from "@/views/AboutView.vue";
import {all} from "axios";

// @ is an alias to /src
export default {
  name: 'adminBookManagement',
  components: {
  },
  data() {
    return{
      all: 0,
      form:{},//
      form1:{},//
      isbn: '',//isbn    isbn
      dialogVisible:false,
      dialogVisible1:false,
      tableData: [],
      userId: this.$route.query.userId,//id
      imageUrlshow: '',
      imageUrl: ''

    }
  },
  created() {
    this.list()
  },
  methods: {
    Search(search){
      // alert("search="+search)
      if (search===null||search===""){
        this.$message({//
          type: "warning", message: ""})
      }else {
        request.post("/api/selectBooksByName",null,{params:{bookname:search}}).then((res)=>{
          this.tableData=res.data;
        })
      }
    },
    handleAvatarSuccess(response, file) {
      // 
      // alert("res:"+response)
      // alert("file:"+file)
      this.$message({
        type: "success", message: ""
      })
      console.log(response)
      console.log(file)
      // alert("this.isbn1="+this.isbn)
      // alert("response.data="+response.data)
      // alert("response.msg:"+response.msg)
      // this.imageUrl = "http://localhost:9090/f74b28a2-5fae-4438-87e2-004edc4d7797.jpg";
      // this.imageUrl = "http://localhost:9090/ktcat.jpg";//
      // this.imageUrl = URL.createObjectURL(file.raw);//
      this.imageUrl = response.msg
      // alert("this.imageUrl:"+this.imageUrl);
      // console.log(file)
      console.log(file.raw);
    },
    beforeAvatarUpload(file) {
      // alert("this.isbn2="+this.isbn)
      // 
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!(isJPG || isPNG)) {
        this.$message.error(' JPG  PNG !');
      }

      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }

      return (isJPG || isPNG) && isLt2M;
    },
    downBook(row){
      // alert(row.isbn)
      // borrow0
          request.get("/api/getBorrow", { params: { isbn: row.isbn } }).then(res=>{
            // alert(res.data)
            if(res.data<=0){
              request.delete("/api/deleteBook",{params:{isbn:row.isbn}}).then(res=> {
                this.list()
                this.$message({
                  type: "success", message: ""
                })
              })

            }else{
              this.list()
              this.$message({
                type:"warning", message: ""
              })
              this.$message({
                type:"error", message: ""
              })
            }
          })
    },
    show1(row){

      this.isbn=row.isbn;
      // alert(row.imageUrl)
      //
      // this.imageUrl=row.imageUrl;
      // alert("ISBN-"+this.isbn)
      this.dialogVisible1=true;
      this.form1=JSON.parse(JSON.stringify(row))// rowjsonform1
    },
    changeBookIfo(){
      // lastnumsallnums
      //isbn  lastnumsallnumsborrownums book1  
      request.post("/api/getNums",null,{params:{isbn:this.isbn}}).then(res=>{
        //
       // alert( this.form1.addnums)
       //  alert(res.data.lastnums)
       //  alert(res.data.allnums)
        if(this.form1.addnums===undefined)
        {
          // alert("")
          this.form1.addnums=0
        }
        if(this.form1.subnums===undefined)
        {
          this.form1.subnums=0
        }
        // alert(this.form1.subnums)   // allnums+addnums  lastnums-subnums 
        let addlastnum=(Number(res.data.lastnums)+Number(this.form1.addnums)-Number(this.form1.subnums)).toString();//
        let addallnums=(Number(res.data.allnums)+Number(this.form1.addnums)-Number(this.form1.subnums)).toString();//
        if(addlastnum<0){
          this.$message({
                    type:"warning", message: ""
                  })
          this.$message({
            type: "error", message: ""
          })
        }else{
          let book1={
            isbn:this.isbn,
            category:this.form1.category,
            bookname:this.form1.bookname,
            author:this.form1.author,
            publish:this.form1.publish,
            img:this.imageUrl,
            lastnums :addlastnum,
            allnums  :addallnums,
            borrownums : res.data.borrownums
          }//
          // alert(book1.lastnums)
          // alert(book1.allnums)
          // alert(book1.borrownums)
          request.put("/api/changeBookInfo",book1 ).then(res=>{
            if (res.code === "200") {
              this.$message({
                type: "success", message: ""
              })
            } else {
              this.$message({
                type: "error", message: ""
              })}
            this.dialogVisible1=false //
            this.list() // 
          })
        }
      })
    },
    save(){
      //
      this.form.lastnums=this.form.allnums
      this.form.borrownums=0
      // alert(this.form.allnums)
      // alert("-----------------")
      // alert(this.form.lastnums)
      //allnums==lastnums borrownums=0 form 
            request.put("/api/addBook",this.form).then(res=>{
              if (res.code === "200") {
                this.$message({
                  type: "success", message: ""
                })
              } else {
                this.$message({
                  type: "error", message: ""
                })}
              this.dialogVisible=false //
              this.list() // 
            })
    },
    addBook(){
      //
      this.dialogVisible = true;//
    },
    list(){

      this.all=0
        //book
      request.get("/api/showBook").then(res=> {
        res.data.forEach(item=>{//lastnumsallnums
                // console.log(item.allnums);
          item.numss = item.lastnums+"/"+item.allnums // 9/10
          // alert(item.lastnums);

          // alert(item.lastnums)
          this.all+=parseInt(item.lastnums)
          // this.all+=item.lastnums

          console.log("-----------------------")
           console.log(item)

          console.log("------------------------")
          // lastnums=0
        })
        // alert("111"+this.all)
        //tableData[]
        this.tableData = res.data
      })
      // alert(this.all)
      // this.tableData.img="http://localhost:9090/b52215cf-451d-49e8-b560-375af9e4db96.jpg"
    },
    return1() {
      console.log("")
      this.$router.push({path:'/adminhome',query: { userId:this.userId}});
    },
    changePassword_fun() {
      console.log("")
      this.$router.push({path:'/adminchangePassword',query: { userId:this.userId}});
    },
    logout() {
      console.log("")
      this.$router.push({path:'/',query: { userId:this.userId}});
    },
    adminpersonalInfo(){
      this.$router.push({path:'/adminpersonalInfo',query: { userId:this.userId}})
    },
    adminuserManagement(){
      this.$router.push({path:'/adminUserManagement',query: { userId:this.userId}})
    },adminbookManagement() {
      this.$router.push({path:'/adminBookManagement',query: { userId:this.userId}});
    }
  }
}
</script>
<style>
  .logoIn{
  color: white;
  font-size: 25px;
  margin: 10px 50px;
  }

</style>
