<template>
  <div class="h-6" />
  <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      @select="handleSelect"
  >
    <div class="logoIn"></div>
  </el-menu>
  <div style="display: flex;">
      <!--  -->
      <div style="width: 150px; min-height: calc(100vh - 62px); overflow: hidden;background-color: white; margin-right: 2px;float: left;">
        <el-menu :default-active="$route.path" router class="el-menu-demo" style="margin-bottom: 10px;" >
      <el-menu-item index="1" @click="return1()">
      
      </el-menu-item>

      <el-sub-menu index="2" >
      <template #title open></template>
        <el-menu-item index="2-1" @click="personalInfo()" ></el-menu-item>
        <el-menu-item index="2-2" @click="myBorrow()"></el-menu-item>
        <el-menu-item index="2-2" @click="borrowRecord()"></el-menu-item>
        <el-menu-item index="2-3" @click="changePassword_fun()"></el-menu-item>
        <el-menu-item index="2-4" @click="logout()"></el-menu-item>
      </el-sub-menu>
           </el-menu>

      </div>
      <div style="width: 100%;">

        <el-table :data="tableData" style="width: 160% ;" >
    <el-table-column
        prop="isbn"
        label="isbn" sortable
    >
    </el-table-column>
    <el-table-column
        prop="requestId"
        label=""
    >
    </el-table-column>
    <el-table-column
        prop="bookNumber"
        label="()"
d    >
    </el-table-column>
    <el-table-column
        prop="category"
        label="" >
    </el-table-column>
    <el-table-column
        prop="bookname"
        label="">
    </el-table-column>
    <el-table-column
        prop="author"
        label="">
    </el-table-column>
    <el-table-column
        prop="publish"
        label="">
    </el-table-column>
    <el-table-column prop="img" label="">
      <template #default="scope">
        <img :src="scope.row.img" alt="" style="width: 100px; height: auto;">
      </template>
    </el-table-column>
    <el-table-column
        prop="borrowState_ture"
        label="">  <!--0  1-->
    </el-table-column>
    <el-table-column fixed="right" label="" width="100">
      <template #default="scope"> <!--scope.row -->
        <el-popconfirm
            title="" @confirm="borrowreturn(scope.row)">
          <template #reference>
            <el-button size="mini" type="text"></el-button>
          </template>
        </el-popconfirm>
      </template>
    </el-table-column>
  </el-table>
      </div>
    </div>
  <div></div>

</template>

<script>
// axios @src
import request from "@/utils/request";
import AboutView from "@/views/AboutView.vue";


// @ is an alias to /src
export default {
  name: 'myBorrowBook',
  components: {
  },
  data() {
    return{
      tableData: [],
      userId:this.$route.query.userId//id id id 
    }
  },
  created() {//list(), 
    // this.list()
    // const userId = this.$route.query.userId;//id
    // alert("myBorrowid="+userId);
    this.list();

  },
  methods: {
    list(){
      //
      request.get("/api/getMyborrow", { params: { id: this.userId } }).then(res=>{
        if(res.code==="200"){
          res.data.forEach(item=>{
            console.log(item.borrowState);
            if(item.borrowState===0){ //0
              item.borrowState_ture=""
            }else if(item.borrowState===1){
              item.borrowState_ture=""
            }
          })
          this.tableData=res.data
        }else{
          this.$message({//
            type: "error", message: res.msg})
        }
      })
    },
    borrowreturn(row){
      //isbnlastnums++
      // alert(row.isbn)
      //idrequest
      //   alert(row.requestId)
      request.delete("/api/DeleteRequest",{ params: { requestId: row.requestId } }).then(res=>{
             this.$message({//
                 type: "success", message: res.msg})

        request.put("/api/lastNumsAdd",null,{params:{isbn:row.isbn}}).then(res=>{
          this.$message({
            type: "success", message: ""})
        })
        this.list();
      })
    },
    personalInfo(){ //
      this.$router.push({path:'/personalInfo',query: { userId:this.userId}})

    },
    myBorrow(){ //
      this.$router.push({path:'/myBorrow',query: { userId:this.userId}})
    },
    return1() {
      console.log("")
      this.$router.push({path:'/userhome',query: { userId:this.userId}});
    },
    changePassword_fun() {
      console.log("")
      this.$router.push({path:'/changePassword',query: { userId:this.userId}});
    },
    logout() {
      console.log("")
      this.$router.push({path:'/',query: { userId:this.userId}});
    },  borrowRecord(){
      console.log("")
      this.$router.push({path: '/borrowRecord', query: {userId: this.userId}});
    }
  }
}
</script>
