<template>
  <div class="img_box"/> <!--  -->
  <div class="flex-center" > <!--  flex-center  -->
    <div>
      <el-card style="width: 800px; margin-top: 20px; margin-left: 20px; opacity: 0.9;" shadow="hover">
        <el-form label-width="80px" size="small">
          <el-upload
              class="avatar-uploader"
              action="/api/uploadAvatar"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
              :data="{id:this.userId}"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <el-form-item label="id" style="margin-left: 200px; margin-top: -150px;">
            <el-input v-model="form.id" disabled autocomplete="off" style="width: 400px"></el-input>
          </el-form-item>
          <el-form-item label="" style="margin-left: 200px">
            <el-input v-model="form.name" autocomplete="off" style="width: 400px"></el-input>
          </el-form-item>
          <el-form-item label="" style="margin-left: 200px">
            <el-select v-model="form.gender" placeholder="" style="width: 400px">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" style="margin-left: 200px">
            <el-input v-model="form.email" autocomplete="off" style="width: 400px"></el-input>
          </el-form-item>
          <el-form-item label="" style="margin-left: 200px">
            <el-input v-model="form.mobile" autocomplete="off" style="width: 400px"></el-input>
          </el-form-item>
          <el-form-item label="" style="margin-left: 200px" >
            <el-input type="textarea" v-model="form.addr" autocomplete="off" style="width: 400px"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="save"> </el-button>
            <!--          <el-button type="primary" @click="sign"><i class="el-icon-location" /></el-button>-->
            <el-button type="primary" @click="return1"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>

import request from "@/utils/request";
export default {
  name: "Person",
  components: {},
  data() {

    return {
      imageUrl: '', // 

      userId: this.$route.query.userId,//id
      form: {
      },
      options: [{
        value: 1,
        label: ''
      }, {
        value: 0,
        label: ''
      }],
    }
  },
  created() {
    this.list()//
  },
  methods: {
    handleAvatarSuccess(response, file) {

      console.log(response)
      console.log(file)


      this.imageUrl = response.msg

      console.log(file.raw);
    },
    beforeAvatarUpload(file) {
      // 
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!(isJPG || isPNG)) {
        this.$message.error(' JPG  PNG !');
      }

      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }

      return (isJPG || isPNG) && isLt2M;
    },
    list(){
      request.get("/api/getUserInfo", { params: { id: this.userId } }).then(res=>{
        console.log(res.data);
        this.form=res.data//
      })
      request.get("/api/getImgUrl", { params: { id: this.userId } }).then(res=>{
        this.imageUrl = res.msg;
        // alert("res.msg="+res.msg)
      }).catch(err=>{
        alert("")
      })
    },
    save() {//

      request.put("/api/changeUserInfo",this.form).then(res => {
        if (res.code==="200") {
          this.$message({
            type: "success", message: ""})
          this.list()
        } else {
          this.$message({
            type: "error", message: ""})
        }
      })
    },
    return1() {
      console.log("")
      this.$router.push({path:'/adminhome',query: { userId:this.userId}});
    }
  }
}
</script>

<style>

.img_box{ /**/
  width: 100%;height: 100%;
  background-image: url(https://library-weibb.oss-cn-hangzhou.aliyuncs.com/Front/%E7%94%A8%E6%88%B7%E7%95%8C%E9%9D%A2.jpg);
  background-size: cover; /*  */
  position: absolute; /*  */
  /* overflow: hidden; */
  /* overflow: auto; */
  z-index: -1;
  background-repeat: no-repeat;
}
/*  */

/**/
.flex-center {
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  height: 100vh; /*  */
}

.avatar-uploader {
  text-align: left;
  padding-bottom: 10px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 138px;
  height: 138px;
  line-height: 138px;
  text-align: center;
}
.avatar {
  width: 160px;
  height: 160px;
  display: block;
}
</style>
