<template>
  <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      @select="handleSelect"
  >
    <el-menu-item index="1" @click="return1()"></el-menu-item>
    <el-sub-menu index="2">
      <template #title></template>
      <el-menu-item index="2-1" @click="personalInfo()" ></el-menu-item>
      <el-menu-item index="2-2" @click="myBorrow()"></el-menu-item>
      <el-menu-item index="2-2" @click="borrowRecord()"></el-menu-item>
      <el-menu-item index="2-3" @click="changePassword_fun()"></el-menu-item>
      <el-menu-item index="2-4" @click="logout()"></el-menu-item>
    </el-sub-menu>
  </el-menu>
  <div class="img_box"/>
  <div class="container">
  <el-form :model="form" :rules="rules" ref="form" label-width="120px" class="demo-ruleForm">
    <el-form-item label="" prop="oldPassword">
      <el-input type="password" v-model="form.oldPassword" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="newPassword">
      <el-input type="password" v-model="form.newPassword" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('form')"></el-button>
      <el-button @click="resetForm('form')"></el-button>
      <el-button @click="retuenmain()"></el-button>
    </el-form-item>
  </el-form>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  data() {
    return {
      userId: this.$route.query.userId,//id
      form: {
        oldPassword: '',
        newPassword: '',
        //  confirm  validatePass2 
        // confirm: '',
      }
    };
  },
  methods: {
    submitForm(){
          if(this.form.newPassword === ""){
            this.$message({
              type: "error", message: ""
            })
          }else if(this.form.oldPassword===""){
        this.$message({
          type: "error", message: ""
        })
      }else {
            // idoldpassword true false id
              request.get("/api/getUserPassword",{params:{id:this.userId,oldPassword:this.form.oldPassword}}).then(res => {
                   if (res.data===false)
                   {
                     this.$message({
                       type: "error", message: ""
                     })
                   }else{
                     request.put("/api/updatePassword",null,{params:{id:this.userId,newPassword:this.form.newPassword}}).then(res=>{
                       this.$message({
                         type: "success", message: ""
                       })
                     }).catch(err=>{
                       this.$message({
                         type: "error", message: ""
                       })
                     })
                   }
              })
          }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    personalInfo(){ //
      this.$router.push({path:'/personalInfo',query: { userId:this.userId}})

    },
    myBorrow(){ //
      this.$router.push({path:'/myBorrow',query: { userId:this.userId}})
    },
    return1() {
      console.log("")
      this.$router.push({path:'/userhome',query: { userId:this.userId}});
    },
    changePassword_fun() {
      console.log("")
      this.$router.push({path:'/changePassword',query: { userId:this.userId}});
    },
    logout() {
      console.log("")
      this.$router.push({path:'/',query: { userId:this.userId}});
    },
    retuenmain() {
      console.log("")
      this.$router.push({path:'/userhome',query: { userId:this.userId}});
    }, borrowRecord(){
      console.log("")
      this.$router.push({path: '/borrowRecord', query: {userId: this.userId}});
    }
  }
};
</script>

<style scoped>
/*  */
.container {
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  height: 100vh; /*  */
  /*  */
  background-color: rgba(255, 255, 255, 0.5); /*  */
  padding: 20px; /*  */
}

.img_box{ /**/
  width: 100%;height: 100%;
  background-image: url(https://library-weibb.oss-cn-hangzhou.aliyuncs.com/Front/%E4%BF%AE%E6%94%B9%E5%AF%86%E7%A0%81%E7%8C%AB.jpg);
  background-size: cover; /*  */
  position: absolute; /*  */
  /* overflow: hidden; */
  /* overflow: auto; */
  z-index: -1;
  background-repeat: no-repeat;
}
</style>
