<template>
  <!---->
  <div class="h-6" />
  <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      @select="handleSelect"
  >
     <div class="logoIn"></div>
  </el-menu>

  <div>
<!--  -->
    <div style="display: flex">
      <!--  -->
      <div style="width: 150px; min-height: calc(100vh - 62px); overflow: hidden;background-color: white; margin-right: 2px;float: left;">
        <el-menu :default-active="$route.path" router class="el-menu-demo" style="margin-bottom: 10px;" >
      <el-menu-item index="1" @click="return1() " >
      </el-menu-item>

      <el-sub-menu index="2" >
      <template #title open></template>
        <el-menu-item index="2-1" @click="adminpersonalInfo()"></el-menu-item>
        <el-menu-item index="2-2"@click="adminuserManagement()"></el-menu-item>
        <el-menu-item index="2-3" @click="adminbookManagement()" ></el-menu-item>
        <el-menu-item index="2-3" @click="changePassword_fun()"></el-menu-item>
        <el-menu-item index="2-4" @click="logout()"></el-menu-item>
      </el-sub-menu>
           </el-menu>

      </div>
      <div style="width: 100%;float: left;">
        <!--  width,  -->
    <el-table :data="tableData" >
      <el-table-column
          prop="requestId"
          label="" sortable
           width="80px"
      >
      </el-table-column>
      <el-table-column
          prop="name"
          label=""
           width="100px"
          >
      </el-table-column>
      <el-table-column
          prop="mobile"
          label=""
           width="120px">
      </el-table-column>
      <el-table-column
          prop="isbn"
          label="isbn"
           width="60px">
      </el-table-column>
      <el-table-column
          prop="bookNumber"
          label="()"
           width="200px">
      </el-table-column>
      <el-table-column
          prop="bookname"
          label="">
      </el-table-column>
      <el-table-column
          prop="borrowState_true"
          label="">
      </el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template #default="scope"> <!--scope.row -->
          <el-popconfirm
              title="" @confirm="agree(scope.row)">
            <template #reference>
              <el-button size="mini" type="text"></el-button>
            </template>
          </el-popconfirm>
          <el-popconfirm
              title="" @confirm="disagree(scope.row)">
            <template #reference>
              <el-button size="mini" type="text"></el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
      </div>
    </div>



  </div>
</template>

<script>
// axios @src
import request from "@/utils/request";
import AboutView from "@/views/AboutView.vue";
import adminUserManagement from "@/views/adminUserManagement.vue";
import adminBookManagement from "@/views/adminBookManagement.vue";
import Color from "element-plus/es/components/color-picker/src/utils/color";


// @ is an alias to /src
export default {
  name: 'HomeView',
  computed: {
    adminBookManagement() {
      return adminBookManagement
    },
    adminUserManagement() {
      return adminUserManagement
    },
    AboutView() {
      return AboutView
    }
  },
  components: {
  },
  data(){
    //
    return{
      userId: this.$route.query.userId,//id
      currentPage:1,//1
      pageSize:5,// 5
      total:10,//

      form:{},//
      dialogVisible:false,//
      search:'',
      tableData: []
    }
  },
  created() {//list(), 
    this.list()
  },
  methods:{
    //
//  pageSize
    handlePageSizeChange(pageSize){
      this.pageSize=pageSize//pageSize
      this.list()
    },
// handleCurrentChange
    handleCurrentChange(pageNum){
      this.currentPage=pageNum
      this.list()
    } ,


    disagree(row){
      // alert(row.requestId)
      // alert(row.borrowState)


      //requestIdrequest
      // alert(row.requestId)
      request.delete("/api/DeleteRequest",{ params: { requestId: row.requestId } }).then(res=>{
        this.$message({//
          type: "success", message: ""})
//  
        request.put("/api/lastNumsAdd",null,{params:{isbn:row.isbn}}).then(res=>{
          this.$message({//
            type: "success", message: ""})
        })

        this.list();//
      })
    },
    agree(row){
      // requestIdborrowState1
      // alert("------------")
      // alert(row.requestId)
      // alert("------------")
      //lastnums-- --
      request
          request.put("/api/updateBorrowState",null,{ params:{ requestId: row.requestId }}).then(res=>{
            this.$message({//
              type: "success", message: ""})
            this.list();//
          })
    },
    list(){
      // request.get("/api/furns").then(res=>{
      //   //tableData[]
      //   this.tableData=res.data
      // })
      request.get("/api/adminRequest").then(res=>{
        console.log(res.data)
        if(res.code==="200"){
          res.data.forEach(item=>{
            console.log(item.borrowState);
            if(item.borrowState===0){ //0
              item.borrowState_true=""
            }
          })
          this.tableData=res.data
        }else{
          this.$message({//
            type: "error", message: res.msg})
        }
      })
    },
    //
    save(){
      if(this.form.id){
        //
        request.put("/api/update",this.form).then(res=>{
          if(res.code==="200"){
            //
            this.$message({ //
              type: "success", message: ""
            })
          }else{
            //
            this.$message({//
              type: "error", message: ""
            })
          }
          this.list() //
          this.dialogVisible = false//
        })
      }else{
        //
        request.post("/api/save",this.form).then(//formjson 
            res=>{//
              // res
              console.log("res=",res)
              this.dialogVisible=false
              this.list()//
            }
        )
      }
    },
    handleEdit(row){
      //row
      this.form=JSON.parse(JSON.stringify(row))
      this.dialogVisible=true
    },
    //add  
    add(){
      this.dialogVisible=true
      //
      this.form={}
    },
    aboutView(){
      this.$router.push('/about')
    },
    adminpersonalInfo(){
      this.$router.push({path:'/adminpersonalInfo',query: { userId:this.userId}})
    },
    adminuserManagement(){
      this.$router.push({path:'/adminUserManagement',query: { userId:this.userId}})
    },return1() {
      console.log("")
      this.$router.push({path:'/adminhome',query: { userId:this.userId}});
    },adminbookManagement() {
      console.log("")
      this.$router.push({path:'/adminBookManagement',query: { userId:this.userId}});
    },changePassword_fun() {
      console.log("")
      this.$router.push({path:'/adminchangePassword',query: { userId:this.userId}});
    }, logout() {
      console.log("")
      this.$router.push({path:'/',query: { userId:this.userId}});
    }
  }
}
</script>
