<template>
  <div class="img_box"/>
  <div class="login">
    <div class="flex-center"> <!--  flex-center  -->
      <el-form class="login-container">
      <h1 class="title" style="color: dodgerblue;"></h1>

      <el-form-item label="" >
        <el-form :model="admin" :rules="rules" ref="loginForm"></el-form>
        <el-input  type="text" v-model="name" placeholder="" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="password" v-model="pwd" placeholder="" autocomplete="off" size="medium" ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" @click="doSubmit()"></el-button>
      </el-form-item>
      <el-row style="text-align: center;margin-top:10px">
        <el-link style="color: dodgerblue; margin-right: 160px " type="primary" @click="forgetPass()"></el-link>
        <el-link style="color: dodgerblue" type="primary" @click="gotoRegister()"></el-link>
      </el-row>
    </el-form>
    </div>
  </div>
</template>

<script>
/* import axios from 'axios'
 import qs from 'qs' */
import { h } from 'vue'
import {ElMessage} from "element-plus";
import request from "@/utils/request";
import Cookies from 'js-cookie'

export default {
  name: 'Login',
  data(){ //
    return {
      userId: this.$route.query.userId,//id
      name:'',
      pwd:''
    }
  },
  //
  methods:{
    forgetPass(){
      this.$message({ // 
        type: "info", message: ""
      })
    },

    gotoRegister(){
      this.$router.push('/Register');//
    },
    doSubmit() {
      let userid;
      // alert(this.name + this.pwd);
//http:///8.135.237.176:80/api/login
      //  i
      request.post("/api/login", { name: this.name, pwd: this.pwd }).then(res => {
        console.log(res);
        if (res.code === "200") {
          this.$message({ // 
            type: "success", message: res.msg
          });

          // nameid Id
          request.get("/api/getUserId", { params: { username: this.name } }).then(res => {
            if (res.data) { // res.data
              // alert("loginid1=" + res.data);
              userid = res.data;
              // alert("userid1=" + userid);
              // 
            request.get("/api/Getadmin",{ params: { id: userid } }).then(res => {
               if (res.data==0) {
                 this.$message({ // 
                   type: "success", message: ""
                 });
                 // useridID
                 this.$router.push({ path: '/userhome', query: { userId: userid } });
               }else if(res.data==1){
                 this.$message({ // 
                   type: "success", message: ""
                 });
                 // useridID
                 this.$router.push({ path: '/adminhome', query: { userId: userid } });
               }
            })

            } else {
              this.$message({ // 
                type: "error", message: "id"
              });
            }
          }).catch(err => {
            console.log(err);
            this.$message({ // 
              type: "error", message: "id"
            });
          });
        }else {
          this.$message({ // 
            type: "error", message: ""
          });
        }
      }).catch(err => {
        // 
        console.log(err);
        this.$message({ // 
          type: "error", message: ""
        });
      });

    }
  }
}
</script>


<style scoped>

.img_box{ /**/
width: 100%;height: 100%;
background-image: url(https://library-weibb.oss-cn-hangzhou.aliyuncs.com/Front/%E7%99%BB%E5%BD%95%E7%8C%AB%E7%8C%AB.jpg);
background-size: cover; /*  */
position: absolute; /*  */
/* overflow: hidden; */
/* overflow: auto; */
z-index: -1;
background-repeat: no-repeat;
}
/*  */

/**/
.flex-center {
  display: flex;
  justify-content: center; /*  */
  align-items: center; /*  */
  height: 100vh; /*  */
}

.login-container {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background-color: rgba(255, 255, 255, 0.6); /*  */
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>
