<script>
// axios @src
import request from "@/utils/request";
import AboutView from "@/views/AboutView.vue";
import changePassword from "@/views/changePassword.vue";
import { v4 as uuidv4 } from 'uuid';//

// @ is an alias to /src
export default {
  name: 'HomeView',
  computed: {
    changePassword() {
      return changePassword
    },
    AboutView() {
      return AboutView
    }
  },
  components: {
  },
  data(){
    //
    return{
      accountBalance: null,//

      currentPage:1,//1
      pageSize:5,// 5
      total:'',//

      form:{},//
      dialogVisible:false,//
      search:'',
      tableData: [],

      registerName:this.$route.query.name,//name
      userId: this.$route.query.userId,//id
      isbn: '',
      borrowState: 0,
      bookNumber:"",
      dialogVisible1:false,
      form1:{}
    }
  },
  created() {//list(), 

    const registerName = this.registerName;//id
    // alert("-"+registerName)
    if(registerName!=null) {
      //id getid id
      // alert("id")
      // alert(registerName)
      this.registerGetId(registerName)
    }
    if(this.userId !=null){
      this.list()
    }

    // alert("id="+userId);
  },
  methods:{
    showMoney(){ //
      // alert(this.userId);
        request.get("/api/getMoney",{params:{id:this.userId}}).then(res=>{
          request.get("/api/allPenalty",{params:{id:this.userId}}).then(res1=>{
                  // alert("="+res1.data)
                  //  alert(res.data)
            var num1 = +res.data;
            var num2 = +res1.data;
            var result = num1 - num2;
            // alert("=" + result);
            this.accountBalance=result;
          })
        })
    },
    changeBookIfo(){
        // alert(this.form1.addMoney)
      request.put("/api/addMoney",null,{params:{id:this.userId,money: this.form1.addMoney}}).then(res=>{
        this.$message({
          type: "success", message: ""
        })
        this.showMoney()
        this.dialogVisible1 = false
      })
    },
    addMoney(){
      this.dialogVisible1=true
    },
    Search(search){
      // alert("search="+search)
      if (search===null||search===""){
        this.$message({//
          type: "warning", message: ""})
      }else {
        request.post("/api/selectBooksByName",null,{params:{bookname:search}}).then((res)=>{
          this.tableData=res.data;
        })
      }
    },
    registerGetId(registerName){
      // alert("="+registerName)
      request.get("/api/getUserId", { params: { username: registerName } }).then(res => {
        // alert("id="+res.data)
        if (res.data) { // res.data
          // alert("loginid1=" + res.data);
          this.userId = res.data;
        }
        this.list()
      }).catch(err => {
        console.log(err);
        // alert("id");
      });
    },

    //
//  pageSize
    handlePageSizeChange(pageSize){

        this.pageSize=pageSize//pageSize
        this.list()
    },
// handleCurrentChange
    handleCurrentChange(currentPage){

      this.currentPage=currentPage//pageSize
      this.list()
    } ,

    handleEdit2(row){
      request.get("/api/find/"+row.id).then(res=>{
        if(res.code==="200"){
          //
          this.form=res.data
        }else{
          this.$message({//
            type: "error", message: res.msg})
        }
      })
      // this.dialogVisible=true//
    },
    //
    borrow(row){
      // alert("isbn"+row.isbn)
      // // alert("isbn"+row.id)
      // alert("userHomeViewid"+this.userId)
      // alert(""+row.lastnums)
      // alert(""+row.allnums)
      // UUID
      const fullUuid = uuidv4(); // "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx"
      // 1010
      // UUID10ID
      const shortId = fullUuid.substring(0, 8);
      // alert(row.isbn)
      // alert("():"+row.isbn+"-"+shortId)
      let requestbook={
          id:this.userId,
          isbn:row.isbn,
          borrowState:0,
          bookNumber:row.isbn+"-"+shortId
      }
        //0
       if(row.lastnums<=0||row.allnums<=0){
         this.$message({
           type: "error", message: ","
         })
       }else {
         request.post("/api/request",requestbook ).then(res => {
           if (res.code === "200") {
             this.$message({
               type: "success", message: ""
             })
             // alert("isbn"+row.isbn)
             //
             request.put("/api/lastNumsSub",null,{ params:{isbn:row.isbn}}).then(res=>{
               if (res.code === "200") {
                 this.$message({
                   type: "success", message: ""
                 })
               }
               this.list()//
             }).catch(err => {
               this.$message({
                 type: "error", message: ""
               })
             })
           } else {
             this.$message({
               type: "error", message: ""
             })}
           this.list() // 
         })
       }
    },
    //
    list(){

      request.get("/api/booksNums").then(res=>{
         this.total=res.data
      })
      request.get("/api/getbookByPag2", { params: {pageNum:this.currentPage, pageSize:this.pageSize} }).then(res=>{
        //tableData[]
        console.log("22222");
        console.log(res.data);
        res.data.forEach(item=>{//lastnumsallnums

          item.nums = item.lastnums+"/"+item.allnums // 9/10
          // lastnums=0
        })
        this.tableData=res.data
      })
      //list()+
      // request.get("/api/furnsBySearchPage", {
      //   params:
      //       {pageNum:this.currentPage,
      //         pageSize:this.pageSize,
      //         search:this.search//
      //       }
      //     }).then(res=>{
      //   //tableData[]
      //   this.tableData=res.data.records
      //   //
      //   this.total = res.data.total
      // })
      this.showMoney()
    },
    //
    save(){
      if(this.form.id){
        //
        request.put("/api/update",this.form).then(res=>{
          if(res.code==="200"){
            //
            this.$message({ //
              type: "success", message: ""
            })
          }else{
            //
            this.$message({//
              type: "error", message: ""
            })
          }
          this.list() //
          this.dialogVisible = false//
        })
      }else{
        //
        request.post("/api/save",this.form).then(//formjson 
            res=>{//
              // res
              console.log("res=",res)
              this.dialogVisible=false
              this.list()//
            }
        )
      }
    },
    handleEdit(row){
      //row
      this.form=JSON.parse(JSON.stringify(row))
      this.dialogVisible=true
    },
    //add  
    add(){
      this.dialogVisible=true
      //
      this.form={}
    },
    personalInfo(){ // id id
      this.$router.push({path:'/personalInfo',query: { userId:this.userId}})

    },
    myBorrow(){ // id
      this.$router.push({path: '/myBorrow',query: { userId:this.userId} })
    },
    return1() {
      console.log("")
      this.$router.push({path:'/userhome',query: { userId:this.userId}});
    },
    changePassword_fun() {
      console.log("")
      this.$router.push({path:'/changePassword',query: { userId:this.userId}});
    },
    logout() {
  console.log("")
  this.$router.push({path:'/',query: { userId:this.userId}});
},
    aboutView(){
      this.$router.push('/about')
    },  borrowRecord(){
      console.log("")
      this.$router.push({path: '/borrowRecord', query: {userId: this.userId}});
    }
  }
}
</script>
